nav {
  margin-left: -8px;
  margin-right: -8px;
  text-align: center;
  mix-blend-mode: normal;
  background: rgb(75,75,75);
  background-image: url(/assets/images/castle_wall.gif);
  background-repeat: repeat-x;
  margin: 0px 0px 0px;
  padding: 0px 0px 8px 0px;
  position: sticky;
  top: 0;
  z-index: 999998;
}

.nav-button {
  display: inline-block;
  margin-top: 5px;
}

.nav-button{
  background-color: #222;
  color: #fff;
  //background-image: url(/assets/images/.png);
  padding: 5px;
  //border:
}

.current_page {
  background-color: #222;
  color: #fa9;
}

#nav-side {display: none;}

.nav-side {
  display: none;
  background-image: url(/assets/images/98.png);
  color: #000;
  font-family: "ibm plex mono";
  overflow: auto;
  overflow-x: hidden;
  overflow-wrap: break-word;
  display: block;
  //max-width: 100px;
  //width: 100px;
  margin-top: 0px;
  max-height: calc(100% - 36px);
  position:fixed;
  left: 0px;
  z-index: 999999;
}

#openbar{ left: 10px; position: absolute;}
#closebar{ visibility: hidden; left:10px; position: absolute; }

.nav-side::-webkit-scrollbar-track {
  border: 1px solid #000;
  padding: 2px 0;
  background-color: #404040;
}

.nav-side::-webkit-scrollbar {
  width: 10px;
}

.nav-side::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #737272;
  border: 1px solid #000;
}

.nav-button:hover{
  background-color: #000;
  color: #ae3;
}

.content{
  margin: 0px 0px 0px;
  //margin-left: 100px;
  text-align: center;
  border: 3px double #aef;
  border-radius: 2px;
  background-color: rgba(10,10,10,0.75);  
}

body{
  margin: 0px;
}


html {
  background-image: url(/assets/images/126.png);
  font-family:"Times New Roman"; 
  color: #fff;
}
